.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.connection_fail {
  background-color: rgb(32, 32, 32);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100svh;
  font-size: 30px;
  color: red;
}

@media (max-width: 480px){
  .App {
    background-color: rgb(32, 32, 32);
    width: 100svw;
    height: 100svh;
  }

  
  .select_level {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
  }
  .select_level button {
    background-color: rgb(48, 48, 48);
    width: 280px;
    height: 9svh;
    margin: 15px 0;
    border: 2px solid rgb(192, 192, 192);
    border-radius: 10px;
    color: white;
    font-size: 20px;
  }

  
  .select_part {
    width: 100%;
    height: 100%;
  }
  .select_part .header {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 9svh;
  }
  .select_part .header img {
    width: 6svh;
    height: 6svh;
    margin: auto 1svh;
  }
  .select_part .header .level {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: calc(100vw - 19svh);
    padding-left: 3svh;
    color: white;
    font-size: 24px;
  }
  .select_part .header .restudy {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 9svh;
    margin: auto 1svh;
    color: white;
    font-size: 18px;
    font-weight: bolder;
  }
  .select_part .body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 91svh;
  }
  .select_part button {
    background-color: rgb(48, 48, 48);
    width: 280px;
    height: 9svh;
    margin-bottom: 5svh;
    border: 2px solid rgb(192, 192, 192);
    border-radius: 10px;
    color: white;
    font-size: 20px;
  }


  .study .header {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 9svh;
  }
  .study .header .back {
    width: 6svh;
    height: 6svh;
    margin: auto 1svh;
  }
  .study .header .reset {
    width: 4svh;
    height: 4svh;
    margin: auto 2svh;
  }
  .study .header div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: calc(100vw - 16svh);
    color: white;
    font-size: 22px;
  }
  .study .body {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  .study .body .info {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 200px;
    height: 40px;
    margin-top: 70px;
    color: rgb(192, 192, 192);
    font-size: 18px;
  }
  .study .body .info img {
    width: 25px;
    height: 25px;
    margin-right: 9px;
  }
  .study .body .word {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: calc(81svh - 230px);
    color: white;
  }
  .study .body .yomikata {
    font-size: 26px;
    margin: 0 5px;
  }
  .study .body .kanji {
    font-size: 36px;
    margin: 30px 5px;
  }
  .study .body .imi {
    font-size: 22px;
    margin: 0 5px;
  }
  .study .body button {
    background-color: rgb(64, 64, 64);
    border: 2px solid rgb(192, 192, 192);
    border-radius: 10px;
    width: 86vw;
    height: 60px;
    margin-top: 50px;
    margin-bottom: 10px;
    color: white;
    font-size: 18px;
    font-weight: bolder;
  }
  .study .footer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 10svh;
  }
  .study .footer button {
    background-color: rgb(64, 64, 64);
    width: 23vw;
    height: 6.5svh;
    border: 2px solid rgb(192, 192, 192);
    border-radius: 10px;
    color: white;
    font-size: 22px;
  }
  .study .footer div {
    width: 40vw;
    color: white;
    font-size: 22px;
  }
  .wordInfo {
    position: absolute;
    background-color: rgb(48, 48, 48);
    top: 0;
    width: 100vw;
    height: 100svh;
    color: white;
  }
  .wordInfo .word {
    text-align: center;
    font-size: 30px;
    height: 40px;
    margin-top: 20px;
    margin-bottom: 40px;
  }
  .wordInfo .info {
    text-align: center;
    padding: 10px;
    height: calc(100svh - 190px);
    font-size: 16px;
  }
  .wordInfo button {
    background-color: rgb(32, 32, 32);
    border-radius: 10px;
    border: 2px solid white;
    width: 120px;  
    height: 50px;
    margin-bottom: 20px;
    color: white;
    font-size: 20px;
  }
}


